import {
  Component,
  ViewEncapsulation,
  Inject,
  ApplicationRef
} from 'angular2/core';

import { RouteConfig, ROUTER_DIRECTIVES } from 'angular2/router';
import { bindActionCreators } from 'redux';

import * as SessionActions from '../actions/session';
import {loginUser, logoutUser} from '../actions/session';

import { AboutPage } from './about-page';
import { CounterPage } from './counter-page';

import {
  Button,
  Navigator,
  NavigatorItem,
  Logo,
  LoginModal
} from '../components';

@Component({
  selector: 'sample-app',
  directives: [
    ROUTER_DIRECTIVES, Navigator, NavigatorItem,
    LoginModal, Logo, Button
  ],
  // Global styles imported in the app component.
  encapsulation: ViewEncapsulation.None,
  styles: [require('../styles/index.css')],
  template: `
    <div>
      <login-modal
        (onSubmit)="login($event)"
        [hasError]="session.get('hasError', false)"
        [isPending]="session.get('isLoading', false)"
        *ngIf="!isLoggedIn"></login-modal>
      <navigator>
        <navigator-item [mr]=true>
          <logo></logo>
        </navigator-item>
        <navigator-item *ngIf="isLoggedIn" [mr]=true>
          <a [routerLink]="['Counter']"
            class="text-decoration-none">Counter</a>
        </navigator-item>
        <navigator-item *ngIf="isLoggedIn">
          <a [routerLink]="['About']"
            class="text-decoration-none">About Us</a>
        </navigator-item>
        <div class="flex flex-auto"></div>
        <navigator-item *ngIf="isLoggedIn" [mr]=true>
          {{
            session.getIn(['user', 'firstName'], '') + ' ' +
            session.getIn(['user', 'lastName'], '') }}
        </navigator-item>
        <navigator-item [hidden]="!isLoggedIn">
          <button className="bg-red white" (click)="logout()" >
            Logout
          </button>
        </navigator-item>
      </navigator>
      <main>
        <router-outlet *ngIf="isLoggedIn"></router-outlet>
      </main>
    </div>
  `
})
@RouteConfig([
  {
    path: '/counter',
    name: 'Counter',
    component: CounterPage,
    useAsDefault: true
  },
  {
    path: '/about',
    name: 'About',
    component: AboutPage
  }
])
export class SampleApp {
  private disconnect: Function;
  private unsubscribe: Function;
  private isLoggedIn: Boolean;
  private session: any;

  constructor(
    @Inject('ngRedux') private ngRedux,
    private applicationRef: ApplicationRef) {
  }

  ngOnInit() {
    this.disconnect = this.ngRedux.connect(
      this.mapStateToThis,
      this.mapDispatchToThis)(this);

    this.unsubscribe = this.ngRedux.subscribe(() => {
      this.applicationRef.tick();
    });
  }

  ngOnDestroy() {
    this.unsubscribe();
    this.disconnect();
  }

  mapStateToThis(state) {
    return {
      session: state.session,
      isLoggedIn: state.session.get('token', false)
    };
  }

  mapDispatchToThis(dispatch) {
    return {
      login: (credentials) => dispatch(loginUser(credentials)),
      logout: () => dispatch(logoutUser())
    };
  }
};



